
.CustomerOrderDetail{
	@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap');
	* {
		margin: 0;
		padding: 0;
		box-sizing: border-box;
		font-family: 'Roboto', sans-serif;
	}
	
	html, body{
		height: 100%;
	}
	
	/*nav-bar*/
	.navbar-list {
		display: flex;
		justify-content: space-between;
		align-items: center;
		list-style: none;
		background-color: black;
		padding: 15px 0 10px 0;
		color: #fff;
	}
	
	.navbar-list img{
		width: 270px; 
	}
	
	.navbar-list a{
		color: inherit;
		font-size: 15px;
		text-decoration: none;
		margin-top: 20px;
		cursor: pointer;
	}
	
	.navbar-list li {
		margin-right: 30px;
	}
	
	.navbar-list .srch{
		margin-bottom: 0;
		margin-left: 3px;
		font-size: 20px;
	}
	
	.navbar-list a:hover{
		color: #FFD400;
	}
	
	
	/* css từng elements trên navbar */
	
	.left {
		position: relative;
		left: 15px;
	}
	
	.right {
		display: flex;
		position: relative;
		right: 10px;
	}
	.right li:nth-child(1) input {
		height: 40px;
		width: 400px;
		border-radius: 3px;
		border: none;
		padding-left: 10px;
	}
	
	.right li:nth-child(1) i {
		position: relative;
		font-size: 20px;
		color: #333;
		cursor: pointer;
		right: 32px;
		top: 4px;
	}
	
	.right li:nth-child(2) {
		position: relative;
		top: 7px;
	}
	
	.right li:nth-child(2) i {
		font-size: 25px;
		margin-right: 5px;
		position: relative;
	}
	
	/*main*/
	main{
		position: relative;
	}
	/* menu */
	aside {
		width: 15%;
		height: 100%;
		position: absolute;
		background-color: #fff;
		transition: 0.5s;
		box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16), 0 2px 10px 0 rgba(0,0,0,0.12);
		display: none;
		padding-top: 20px;
	} 
	
	ul li{
		list-style: none;
	}
	
	aside a {
		width: 100%;
		display: block;
		padding: 10px 0;
		text-align: center;
		border: none;
		font-size: 20px;
		transition: 0.08s;
		text-decoration: none;
		color: black;
	}
	
	aside a:hover:not(.active) {
		background-color: #DCDCDC;
		cursor: pointer;
	}
	
	aside a.active{
		background-color: #DCDCDC;
	}
	
	/*section*/
	.menu-button {
		background-color: #fff;
		text-align: left;
		border: none;
		font-size: 24px;
		padding: 8px 0;
		text-decoration: none;
		cursor: pointer;
	}
	
	section {
		min-height: 100vh; 
		margin-bottom: 20px;
		margin-left: auto;
		height: 100%;
		padding: 20px 0 20px 40px;
	}
	
	h1{
		text-align: center;
		font-weight: 400;
		font-size: 30px;
	}
	
	h2 {
		margin: 10px 0 20px 0;
	}
	
	hr {
		margin: 16px 0;
	}
	
	table{
		margin: 10px auto;
		width: 90%;
		text-align: center;
		border-collapse: separate;
		border-spacing: 20px;
	}
	
	th{
		font-size: 20px;
	}
	
	.product img{
		width: 100px;
		height: 70px;
	}
	
	.product{
		font-size: 18px;
		box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
		border-radius: 4px;
	}
	
	.product td{
		padding: 10px 0 10px 20px;
		border-radius: 4px;
	}
	
	/*form*/
	form {
		width: 80%;
		margin: 10px auto;
		padding: 10px 0;
	}
	
	.info label {
		display: inline-block;
		width: 150px;
		text-align: right;
		font-size: 18px;
		margin:10px 0;
		font-weight: bold;
	}
	
	form input[type="text"], input[type="number"], select{
		font-size: 16px;
		width: 70%;
		height: 40px;
		padding: 10px;
		margin-top: 5px;
		margin-left: 20px;
		border: 1px solid #ccc;
		border-radius: 3px;
	}
	
	.option label:nth-child(1){
		display: inline-block;
		width: 150px;
		text-align: right;
		font-size: 18px;
		margin:10px 0;
		font-weight: bold;
	}
	
	.option label:nth-child(2), label:nth-child(4){
		font-weight: 550;
		margin-left: 20px;
	}
	
	input[type="radio"]{
		cursor: pointer;
	}
	
	/*button*/
	.buttons{
		width: 200px;
		margin: 30px 0 0 175px;
	}
	
	.buttons i{
		margin-right: 4px;
	}
	
	.buttons a{
		color: black;
		border: 1px solid black;
		width: 50px;
		padding: 5px 10px;
		border-radius: 4px;
		text-decoration: none;
		margin-right: 4px;
		cursor: pointer;
	}
	
	.buttons a:hover{
		border: 1px solid red;
		color: red;
	}
	
	
	
}


